/*------------------------------------*\
    RESET
\*------------------------------------*/
/* http://meyerweb.com/eric/tools/css/reset/ 
    v2.0b1 | 201101 
    NOTE:WORK IN PROGRESS
    USE WITH CAUTION AND TEST WITH ABANDON */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: inherit; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* remember to define visible focus styles! 
:focus{
    outline:?????;
} */
/* remember to highlight inserts somehow! */
ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

#app {
  display: grid;
  grid-template-rows: 108px repeat(2, [row] auto) 539px;
  grid-template-columns: repeat(8, [col] 1fr);
  grid-template-areas: "logo .    .    .    nav  nav  nav  ." "sbar me   me   me   me   me   me   me" "sbar work work work work work work work" "cont cont cont cont cont cont cont cont"; }

.logo {
  grid-area: logo; }

nav {
  grid-area: nav; }
  nav a {
    grid-column-start: 1; }
  nav a:nth-child(2) {
    grid-column-start: 2; }
  nav a:nth-child(3) {
    grid-column-start: 3; }

.sidebar {
  grid-area: sbar; }

section {
  grid-template-columns: repeat(7, 1fr);
  align-items: flex-end; }
  section header {
    grid-area: head; }

section#me {
  grid-area: me;
  grid-template-rows: 208px auto;
  grid-template-areas: "head head head head .    .   ." ".    .    .    p    p    p   p"; }
  section#me p {
    grid-area: p;
    margin: 101px 0 86px 0; }

section#work {
  grid-area: work;
  grid-template-rows: 254px repeat(3, 1fr);
  grid-template-areas: "head head head head .    .    ." "p1 p1 p1 p1 p1 p1 p1" "p2 p2 p2 p2 p2 p2 p2" "p3 p3 p3 p3 p3 p3 p3"; }
  section#work header {
    margin-bottom: 109px;
    align-self: flex-end; }
  section#work ul {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(7, 1fr);
    grid-template-areas: "text text . img img img img"; }
    section#work ul li.text {
      grid-area: text; }
    section#work ul li.image {
      grid-area: img; }
  section#work #project-1 {
    grid-area: p1; }
  section#work #project-2 {
    grid-area: p2; }
  section#work #project-3 {
    grid-area: p3; }

section#contact {
  grid-area: cont;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: 1fr;
  grid-template-areas: ". head head head .    .    . ."  ". p    p    p    p    .    . ." ". foot foot foot foot foot foot ."; }
  section#contact header {
    grid-area: head; }
  section#contact p {
    grid-area: p; }
  section#contact footer {
    grid-area: foot; }

body {
  background-image: url("/public/grid.png");
  background-position: center;
  font-family: 'IBM Plex Sans';
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  font-size: 24px;
  color: #051429;
  color: var(--night-blue);
  text-align: left;
  line-height: normal; }

#app {
  margin: 25px 33px 0 33px; }

header {
  position: relative;
  height: 128px; }
  header div.rectangle {
    position: absolute;
    background-color: #183eef;
    top: 70px;
    z-index: -1;
    height: 50%;
    width: 100%; }
  header h1 {
    font-size: 95px;
    font-weight: bold;
    line-height: 1.29;
    letter-spacing: -0.5px;
    z-index: 1; }

h4 {
  font-weight: bold; }

h3 {
  font-size: 36px;
  font-weight: bold;
  letter-spacing: -0.2px;
  margin-bottom: 49px; }

img {
  height: 505px; }

p {
  opacity: 0.8;
  text-align: justify;
  color: #051429;
  color: var(--night-blue);
  line-height: 2;
  letter-spacing: -0.1px; }

a {
  font-size: 18px;
  font-weight: bold;
  color: #183eef; }



